import React, { useState, useEffect, useRef } from "react";
import TagLine from "../components/TagLine";
import ValidatedInput from "../components/ValidatedInput";
import FormLabel from "../components/FormLabel";
import { HEADERS } from "../constants";

const text = {
  heading: "Elevate your performance",
  subHeading:
    "Get ahead of your competition and be the first to hear when new content is released, by signing up below.",
};

const USERS_URL = "https://ezone-cf03.restdb.io/rest/ezone";

const post = (data) => {
  const postData = JSON.stringify(data);
  return fetch(USERS_URL, {
    method: "post",
    headers: HEADERS,
    body: postData,
  })
    .then((res) => res.json())
    .then((data) => {
      console.log("Posted", data);
    });
};

const Form3 = ({ setStep, setProgress, previous, next, games, areas }) => {
  const [showErrors, setShowErrors] = useState(false);
  const form = useRef(null);
  const handleSubmit = (e) => {
    e.preventDefault();
    setShowErrors(true);

    if (form.current.checkValidity()) {
      const data = {
        fullname: form.current.elements.fullname.value,
        email: form.current.elements.email.value,
        games,
        areas,
      };
      post(data).then(() => {
        setStep(4);
      });
    }
  };

  return (
    <section className="form form3">
      <FormLabel text={"YOUR DETAILS"} number={"03."} />
      <div className="content">
        <form className="personal-form" onSubmit={handleSubmit} ref={form}>
          <TagLine heading={text.heading} subHeading={text.subHeading} />
          <div className="inputs">
            <ValidatedInput
              label={"Full name"}
              name={"fullname"}
              type={"fullname"}
              showErrors={showErrors}
              errorMessage={"Name cannot be empty"}
            />
            <ValidatedInput
              label={"Email"}
              name={"email"}
              type={"email"}
              showErrors={showErrors}
              errorMessage={"Email has to be valid"}
            />
          </div>
          <div className="buttons">
            <button className="button button--link" onClick={() => setStep(2)}>
              Go back
            </button>
            <button className="button" type="submit" onClick={handleSubmit}>
              Sign up
            </button>
          </div>
        </form>
      </div>
    </section>
  );
};

export default Form3;
